body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, 
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,
body {
    height: 100%;
}

h3, h5 {
    margin: 0;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 800;
}

.container {
    display: flex;
    justify-content: space-around;
    flex-flow: wrap;
}

.movie {
    display: flex;
    justify-content: flex-start;
    width: 40%;
    height: 40%;
    margin-top: 100px;
    padding: 10px;
    background-color: white;
    border: 1px solid rgba(0,0,0,0.05);
    box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.2);
}

.movie .movie_data {
    padding-left: 30px;
}

.movie img {
    position: relative;
    width: 120px;
    height: 180px;
    bottom: 50px;
    transition: 0.5s;
}

.movie img:hover {
    transform:scale(1.02);
    transition: 0.5s;
}

.movie .movie_data .genres {
    display: flex;
    flex-wrap: wrap;
    font-size: 13px;
    list-style: none;
    margin: 0;
    padding: 0;
}

.movie .movie_data .genres li {
    margin-right: 10px;
}

.movie .movie_data .movie_rating {
    font-size: 13px;
    margin-top: 10px;
}

.movie .movie_data .movie_summary {
    font-size: 14px;
}

@media screen and (max-width: 1000px) {
    .container {
        flex-wrap: nowrap;
        flex-flow: column;
        align-items: center;
    }

    .movie {
        width: 80%;
    }
}